import React, {useEffect, useState} from 'react';

import Logo from '../../assets/img/logo_green.png';

//Styles
import { Wrapper, Content, LogoImg, Container, NavLink, NavDropdown, NavDropdownBtn } from "./Navbar.styles";
import Dropdown from '../DropDown';

import i18next from "i18next";

const options = ['navbar.myProfile', 'navbar.myFiles'];

const Navbar = () => {
  const [user, setUser] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState([]);

  useEffect(() => {
    setLoading(true)
    fetch("api/users/88")
      .then( res => res.json())
      .then( json => {
        setUser(json['results'])
        setLoading(false)
        console.log(user)
      })
      .catch(err => {
        setError(true)
        setLoading(false)})
  }, [])

  return (
    <div>
      <Wrapper>
        <Content>
          <LogoImg src={Logo} alt='lgoo'/>
          <Container>
            <NavLink to="/">{i18next.t('navbar.explore')}</NavLink>
            <NavLink to="/">{i18next.t('navbar.myClasses')}</NavLink>
            <NavLink to="/">{i18next.t('navbar.myFavourites')}</NavLink>
            <Dropdown brand={i18next.t('navbar.myAccount')} options={options} endOption={i18next.t('navbar.logout')}/>
          </Container>
        </Content>
      </Wrapper>
      <Wrapper>
        {
          loading ? <p>Loading...</p> :
            error ? <p>Error ocurred fetching data</p> :
              (user.map( d => <div>{user}</div>))
        }
      </Wrapper>
    </div>
  );
};

Navbar.propTypes = {};

export default Navbar;